import { TableGit } from '../core/table-git';
import { CellObject } from '../core/cell';
import { ColumnMetadata, RowMetadata, CellValue, CellFormat } from '../types';
import { generateId } from './hash';

/**
 * 创建表格Git实例的便利函数
 */
export function createTableGit(branchName: string = 'main'): TableGit {
  const tableGit = new TableGit();
  tableGit.init(branchName);
  return tableGit;
}

/**
 * 创建单元格的便利函数
 */
export function createCell(
  row: number,
  column: number,
  value: CellValue,
  formula?: string,
  format?: CellFormat
): CellObject {
  return new CellObject(row, column, value, formula, format);
}

/**
 * 创建列元数据的便利函数
 */
export function createColumn(
  id?: string,
  options: {
    description?: string;
    dataType?: 'string' | 'number' | 'date' | 'boolean' | 'mixed';
    width?: number;
    hidden?: boolean;
    order?: number;
    constraints?: {
      required?: boolean;
      unique?: boolean;
      pattern?: string;
      min?: number;
      max?: number;
    };
  } = {}
): ColumnMetadata {
  return {
    id: id || generateId('col_'),
    description: options.description,
    dataType: options.dataType || 'mixed',
    width: options.width || 100,
    hidden: options.hidden || false,
    order: options.order || 0,
    constraints: options.constraints
  };
}

/**
 * 创建行元数据的便利函数
 */
export function createRow(
  options: {
    id?: string;
    height?: number;
    hidden?: boolean;
    order?: number;
  } = {}
): RowMetadata {
  return {
    id: options.id || generateId('row_'),
    height: options.height || 25,
    hidden: options.hidden || false,
    order: options.order || 0
  };
}

/**
 * 创建包含示例数据的表格
 */
export function createSampleTable(): TableGit {
  const repo = createTableGit();
  
  // 添加列定义 - 只需要id，不需要name
  const columns = [
    createColumn('product_name', { 
      dataType: 'string', 
      width: 150, 
      order: 0,
      constraints: { required: true }
    }),
    createColumn('price', { 
      dataType: 'number', 
      width: 100, 
      order: 1,
      constraints: { required: true, min: 0 }
    }),
    createColumn('stock', { 
      dataType: 'number', 
      width: 100, 
      order: 2
    }),
    createColumn('description', { 
      dataType: 'string', 
      width: 200, 
      order: 3
    })
  ];
  
  // 添加列到表格
  columns.forEach(col => repo.addColumn('default', col));
  
  // 初始化行结构（含表头行0）
  // 这里按当前示例数据的行数创建稳定的行ID和顺序
  for (let r = 0; r <= 3; r++) {
    repo.addRow('default', createRow({ id: `row_${r}`, order: r }));
  }
  
  // 第0行作为列头，和普通单元格一样处理
  repo.addCellChange('default', 0, 0, '产品名称', undefined, { fontWeight: 'bold' });
  repo.addCellChange('default', 0, 1, '价格', undefined, { fontWeight: 'bold' });
  repo.addCellChange('default', 0, 2, '库存', undefined, { fontWeight: 'bold' });
  repo.addCellChange('default', 0, 3, '描述', undefined, { fontWeight: 'bold' });
  
  // 添加数据行
  repo.addCellChange('default', 1, 0, 'iPhone 15');
  repo.addCellChange('default', 1, 1, 5999);
  repo.addCellChange('default', 1, 2, 100);
  repo.addCellChange('default', 1, 3, '最新款iPhone');
  
  repo.addCellChange('default', 2, 0, 'MacBook Pro');
  repo.addCellChange('default', 2, 1, 12999);
  repo.addCellChange('default', 2, 2, 50);
  repo.addCellChange('default', 2, 3, '专业级笔记本电脑');
  
  repo.addCellChange('default', 3, 0, 'iPad Air');
  repo.addCellChange('default', 3, 1, 4599);
  repo.addCellChange('default', 3, 2, 75);
  repo.addCellChange('default', 3, 3, '轻薄平板电脑');
  
  // 提交初始数据
  repo.commit('初始化产品表', 'System', 'system@example.com');
  
  return repo;
}
